import React, { useEffect, useState } from 'react';
// import { Link } from 'react-router-dom';

const Header = () => {

    //   
    const [scrollPosition, setScrollPosition] = useState(0);
    const [prevScrollPosition, setPrevScrollPosition] = useState(0);

    //    (  )
    const [headerClass, setHeaderClass] = useState('');

    
    const handleScroll = () => {
        //   
        const currentPosition = window.pageYOffset;
        //setScrollPosition  scrollPosition  position   
        setScrollPosition(currentPosition);
  
        if (currentPosition > prevScrollPosition) {
            //  
            setHeaderClass('scroll-down');
            
        } else if (currentPosition < prevScrollPosition) {
            //  
            setHeaderClass('scroll-up');
        }
    
        if (currentPosition === 0) {
            setHeaderClass('top');
        }
        // setState     currentPosition       
        setPrevScrollPosition(currentPosition);

    }

    //  / 
    useEffect(() => {
        window.addEventListener('scroll', handleScroll);
        return () =>{
            window.removeEventListener('scroll', handleScroll);
        }
    },[prevScrollPosition])

  

    return (
        <>
            <header className={`header ${headerClass}`}>
                <div className="header-inner">
                    <div className='logo'>
                    </div>
                    <ul className='gnb'>
                        <li>
                            
                        </li>
                        <li>
                            
                        </li>
                        <li>
                             
                        </li>
                    </ul>
                </div>
            </header>
        </>
    );
};

export default Header;