import React, { useEffect, useState } from 'react';
import Slider from 'react-slick';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';
import { gsap } from 'gsap';

const ImageSliderComponent: React.FC = () => {
    const [currentSlide, setCurrentSlide] = useState(0);

    const images = [
        { id: 1, path: "/images/main-banner-01.jpg", alt: "1", title: " ", text: "        ,      " },
        { id: 2, path: "/images/main-banner-02.jpg", alt: "2", title: " , ", text: "         ,           ." },
        { id: 3, path: "/images/main-banner-03.jpg", alt: "3", title: "", text: "365,   .       ,   ." },
        { id: 4, path: "/images/main-banner-04.jpg", alt: "4", title: " ", text: "        ,      " },
        { id: 5, path: "/images/main-banner-05.jpg", alt: "5", title: "", text: "           ." },
    ];


    const settings = {
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1,
        autoplay: true,
        autoplaySpeed: 5000,
        cssEase: "linear",
        afterChange: (index: number) => {
            setCurrentSlide(index);
        }
    };


    const handleImageError = (event: React.SyntheticEvent<HTMLImageElement, Event>) => {
        console.error(`Failed to load image: ${event.currentTarget.src}`);
        event.currentTarget.alt = "Image not available";
    };

    return (
        <div className='mainBanner'>
            <Slider {...settings}>
                {images.map((image, index) => (
                    <div key={image.id}>
                        <img src={image.path} alt={image.alt} onError={handleImageError} />
                        <div className='slider-desc' data-aos="fade-up">
                            <div className='slider-desc__title'>{image.title}</div>
                            <div className='slider-desc__text'>{image.text}</div>
                            <button type='button' className='link-btn'>MORE</button>
                        </div>
                        <div className="scroll-indicator">
                            <div className="scroll-line"></div>
                            <span>Scroll</span>
                        </div>
                    </div>
                ))}
            </Slider>
        </div>
    );
};

export default ImageSliderComponent;