import React, { useEffect } from 'react';
import './App.css';
import Router from "./routes/Router";
import Header from './components/layout/Header';
import Footer from './components/layout/Footer';

import AOS from 'aos';
import 'aos/dist/aos.css'; // AOS  


const App: React.FC = () => {
    useEffect(() => {
        AOS.init({
            duration: 1000, //   
            once: false, //      
            offset: 100, //   
        });
        AOS.refresh();
    }, []);

    return (
        <>
            <Header />
            <Router />
            <Footer />
        </>
    );
}

export default App;
